@import url("https://fonts.googleapis.com/css2?family=Inter:wght@500;600&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@500;600;700&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --menu-semi-bold-14: Inter;
  --font-poppins: Poppins;

  /* font sizes */
  --font-size-base: 16px;
  --font-size-lg: 18px;
  --menu-semi-bold-14-size: 14px;

  /* Colors */
  --white: #fff;
  --selected-grey: #353945;
  --color-dimgray-100: #6e6e6e;
  --color-gray-100: #fefefe;
  --for-input-boxes: #242731;
  --color-dark-background: #1b1d21;
  --state-dark-active: #191b20;
  --text-colour: #808191;
  --bg: #000;
  --buffer-blue: #3772ff;
  --light-blue: #a3e3ff;
  --color-primary-cta: #355dff;
  --accent-accent-01: #6c5dd3;

  /* Gaps */
  --gap-base: 16px;

  /* Paddings */
  --padding-3xs: 10px;

  /* border radiuses */
  --br-base: 16px;
  --br-xs: 12px;
  --br-3xs: 10px;
  --br-xl: 20px;
}
